*{
    margin: 0;
    padding: 0;
}
.item{
    margin: 20px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.3);
    padding: 0px 0px 5px 0px;
}
.item img{
    width: 260px;
    height: 320px;
}
.pop_carts{
    display: flex;
    justify-content: space-around;
    margin-top: 10px;
}
.pop_names p{
    font-size: 18px;
    font-weight: 900;
}
.pop_carts button{
    border-radius: 6px;
    color: white;
    background-color: #181C14;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    border: none;
    padding: 12px 18px;
    font-size: 16px;
    align-items: center;
    margin-left: 16px;
    cursor: pointer;
}


/* -----------------media queries------------- */




@media (max-width:768px) {
    .item{
        width: 41.5%;
    }
    .item img{
        width: 217px;
        height: 260px;
    }
    .item p,span{
        font-size: 1rem;
    }
    .item button{
        font-size: .5rem;
    }
}

@media only screen and (max-width: 480px) {
    .item{
        width: 31%;
    }
    .item img{
        width: 117px;
        height: 130px;
    }
    .item p,span{
        font-size: .6rem;
    }
    .item button{
        font-size: .3rem;
        padding: 0px 5px;
      
    }
}
@media only screen and (max-width: 320px) {
    .item{
        width: 42%;
        margin: 10px;
    }
    .item img{
        width: 117px;
        height: 130px;
    }
    .item p,span{
        font-size: .6rem;
    }
    .item button{
        font-size: .3rem;
        padding: 0px 8px;
      
    }
}