*{
    margin: 0;
    padding: 0;
}
.coffee{
    margin: 50px 150px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.upper{
    display: flex;
    width: 100%;
    justify-content: space-between;
    margin-bottom: 1rem;
}
.upper p{
    font-size: 2rem;
}
.btnsorder button{
    border-radius: 6px;
    color: white;
    background-color: #181C14;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    border: none;
    padding: 12px 18px;
    font-size: 16px;
    align-items: center;
    margin-left: 16px;
    cursor: pointer;
}
.downer{
    display: flex;
    flex-wrap: wrap;
}



/* -------------------------media-queries--------------------- */

@media (max-width:768px) {
    .coffee{
        margin: 50px 81px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .upper{
        display: flex;
        width: 100%;
        justify-content: space-between;
        margin-bottom: 1rem;
    }
    .upper p{
        font-size: 1.5rem;
    }
    .btnsorder button{
        border-radius: 6px;
        color: white;
        background-color: #181C14;
        box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
        border: none;
        padding: 12px 18px;
        font-size: .8rem;
        align-items: center;
        margin-left: 16px;
        cursor: pointer;
    }
    .downer{
        display: flex;
        width: 100%;
        flex-wrap: wrap;
    }
}

@media only screen and (max-width: 480px) {
    .coffee{
        margin: 30px 50px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .upper{
        display: flex;
        width: 100%;
        justify-content: space-between;
        margin-bottom: 1rem;
    }
    .upper p{
        font-size: 1.2rem;
    }
    .btnsorder button{
        border-radius: 6px;
        color: white;
        background-color: #181C14;
        box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
        border: none;
        padding: 10px 14px;
        font-size: .5rem;
        align-items: center;
        margin-left: 10px;
        cursor: pointer;
    }
    .downer{
        display: flex;
        margin-left: 4rem;
        width: 100%;
        flex-wrap: wrap;
    }
}
@media only screen and (max-width: 320px) {
    .coffee{
        margin: 30px 20px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .upper{
        display: flex;
        width: 100%;
        justify-content: space-between;
        margin-bottom: 1rem;
    }
    .upper p{
        font-size: 1rem;
    }
    .btnsorder button{
        border-radius: 6px;
        color: white;
        background-color: #181C14;
        box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
        border: none;
        padding: 8px;
        font-size: .4rem;
        align-items: center;
        margin-left: 10px;
        cursor: pointer;
    }
    .downer{
        display: flex;
        margin: 0px 10px;
        width: 100%;
        flex-wrap: wrap;
    }
}